/*
* Noto Sans TC (Chinese_traditional) http://www.google.com/fonts/earlyaccess
 */
 @font-face {
   font-family: 'Noto Sans TC';
   font-style: normal;
   font-weight: 100;
   src: url('fonts/NotoSansTC-Thin.otf') format('opentype');
 }
 @font-face {
   font-family: 'Noto Sans TC';
   font-style: normal;
   font-weight: 300;
   src: 
        url(fonts/NotoSansTC-Light.otf) format('opentype');
 }
 @font-face {
    font-family: 'Noto Sans TC';
    font-style: normal;
    font-weight: 400;
    src: 
         url(fonts/NotoSansTC-Regular.otf) format('opentype');
  }
 @font-face {
    font-family: 'Noto Sans TC';
    font-style: normal;
    font-weight: 500;
    src: 
         url(fonts/NotoSansTC-Medium.otf) format('opentype');
  }
 @font-face {
    font-family: 'Noto Sans TC';
    font-style: normal;
    font-weight: 700;
    src: 
         url(fonts/NotoSansTC-Bold.otf) format('opentype');
  }
 @font-face {
    font-family: 'Noto Sans TC';
    font-style: normal;
    font-weight: 900;
    src: 
         url(fonts/NotoSansTC-Black.otf) format('opentype');
  }


